* {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
    list-style: none;
    overflow-x: hidden;
}

.App {
    background-color: #333;
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 20px;
}

.Search {
    width: 100%;
    height: 60px;
    position: fixed;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
}

.input {
    margin: 0 30px;
    padding: 0 15px;
    height: 30px;
    border-radius: 5px;
    border: none;
    width: 300px;
}

.input:focus {
    outline: none;
}

.Body {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.card-conteiner {
    max-width: 1000px;
    padding-top: 60px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 10px;
    margin: 0 20px;
}

.Card {
    position: relative;
    background-color: #222;
    padding: 20px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: auto;
    border-radius: 10px;
}

.first-page {
    text-align: center;
}

.second-page {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: 1;
    background-color: rgba(0, 0, 0, 0.698);
    transition: .5s;
}

.second-page:hover {
    opacity: 1;
}

li {
    margin: 5px 0;
}

.types {
    display: flex;
}

.type {
    margin: 0 10px;
}

button {
    background-color: blue;
    padding: 10px;
    border: none;
    color: white;
    border-radius: 5px;
    margin: 10px;
}

button:hover {
    background-color: rgb(0, 0, 205);
}

@media (max-width: 1000px) {
    .card-conteiner {
        grid-template-columns: repeat(3, 1fr);
    }
}

@media (max-width: 600px) {
    .card-conteiner {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 380px) {
    .card-conteiner {
        grid-template-columns: repeat(1, 1fr);
    }
}

.spinner {
    position: absolute;
    width: 9px;
    height: 9px;
}

.spinner div {
    position: absolute;
    width: 50%;
    height: 150%;
    background: #474bff;
    transform: rotate(calc(var(--rotation) * 1deg)) translate(0, calc(var(--translation) * 1%));
    animation: spinner-fzua35 1s calc(var(--delay) * 1s) infinite ease;
}

.spinner div:nth-child(1) {
    --delay: 0.1;
    --rotation: 36;
    --translation: 150;
}

.spinner div:nth-child(2) {
    --delay: 0.2;
    --rotation: 72;
    --translation: 150;
}

.spinner div:nth-child(3) {
    --delay: 0.3;
    --rotation: 108;
    --translation: 150;
}

.spinner div:nth-child(4) {
    --delay: 0.4;
    --rotation: 144;
    --translation: 150;
}

.spinner div:nth-child(5) {
    --delay: 0.5;
    --rotation: 180;
    --translation: 150;
}

.spinner div:nth-child(6) {
    --delay: 0.6;
    --rotation: 216;
    --translation: 150;
}

.spinner div:nth-child(7) {
    --delay: 0.7;
    --rotation: 252;
    --translation: 150;
}

.spinner div:nth-child(8) {
    --delay: 0.8;
    --rotation: 288;
    --translation: 150;
}

.spinner div:nth-child(9) {
    --delay: 0.9;
    --rotation: 324;
    --translation: 150;
}

.spinner div:nth-child(10) {
    --delay: 1;
    --rotation: 360;
    --translation: 150;
}

@keyframes spinner-fzua35 {
    0%,
    10%,
    20%,
    30%,
    50%,
    60%,
    70%,
    80%,
    90%,
    100% {
        transform: rotate(calc(var(--rotation) * 1deg)) translate(0, calc(var(--translation) * 1%));
    }
    50% {
        transform: rotate(calc(var(--rotation) * 1deg)) translate(0, calc(var(--translation) * 1.5%));
    }
}